<template>
  <div>
    <div class="topTitle">
      <span class="topTitle-span1">过程管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">案例库管理</span>
      <div class="button-content">
        <Button type="primary" @click="operate('1')">
          <i class="ivu-icon ivu-icon-md-add"></i>新增
        </Button>
        <Button type="primary" v-authcontrol="{menuCode:'108001003004'}"  @click="exportFn">
          <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
        </Button>
      </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
        </div>
        <div class="contentCellBody1">
          <Form ref="formValidate" :model="formValidate" :label-width="120" inline>
            <Row>
              <FormItem label="案件开始时间：">
                <DatePicker
                  type="daterange"
                  placeholder="请选择案件开始时间"
                  :value="formValidate.permissionTime"
                  @on-change="formValidate.permissionTime=$event"
                  style="width:200px"
                ></DatePicker>
              </FormItem>
              <FormItem label="涉及金融行业：">
                <Input v-model="formValidate.industry" placeholder="请输入涉及金融行业" style="width:200px"></Input>
              </FormItem>
              <FormItem label="案例标题：">
                <Input v-model="formValidate.caseTitle" placeholder="请输入案例标题" style="width:200px"></Input>
              </FormItem>
              <FormItem label="案件结束时间：">
                <DatePicker
                  type="daterange"
                  placeholder="请选择案件结束时间"
                  :value="formValidate.permissionTime1"
                  @on-change="formValidate.permissionTime1=$event"
                  style="width:200px"
                ></DatePicker>
              </FormItem>
              <FormItem label="特征类型：">
                <Input v-model="formValidate.featureType" placeholder="请输入特征类型" style="width:200px"></Input>
              </FormItem>
              <FormItem label="管理部门：">
                <treeselect
                  v-model="formValidate.manager"
                  valueConsistsOf="ALL" 
              :limit="3"
                  :multiple="true"
                  :options="data1"
                  placeholder="请选择管理部门"
                  style="width:200px"
                />
              </FormItem>
              <FormItem label="案件收集时间：">
                <DatePicker
                  type="daterange"
                  placeholder="请选择案件收集时间"
                  :value="formValidate.permissionTime2"
                  @on-change="formValidate.permissionTime2=$event"
                  style="width:200px"
                ></DatePicker>
              </FormItem>
            </Row>
            <Row>
              <div class="btnClass">
                <Button
                  type="primary"
                  @click="handleSubmit(0, 1)"
                  style="width:89px"
                >查询</Button>
                <Button
                  type="primary"
                  style="margin-left:10px"
                  @click="handleSubmit(1, 1)"
                >精确查询</Button>
              </div>
            </Row>
          </Form>
          <div class="contentCellBody">
            <div class="contentCellTitle">
              <div class="contentCellTitle-1"></div>数据列表
            </div>
            <Row>
              <table class="tableBox">
                <thead>
                  <tr>
                    <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                  </tr>
                </thead>
                <tbody>
                  <template v-for="(item,index) in tableBody">
                    <tr>
                      <td>
                        {{pageSize*(pageNum-1)+index+1}}
                      </td>
                      <td class="noWrap">
                        <span class="spanBox" @click="operate('2', item)">修改</span>
                        <span class="spanBox" @click="operate('3', item)">删除</span>
                      </td>
                   
                      <td>
                        <el-popover
                      placement="top-start"
                      title
                      width="200"
                      trigger="hover"
                      :content="item.industry"
                    >
                      <span slot="reference" class="textOverflow  disBlock textMaxWidth">{{item.industry}}</span>
                    </el-popover>
                  </td>
                       <td>
                      <el-popover
                        placement="top-start"
                        title
                        width="200"
                        trigger="hover"
                        :content="item.caseTitle"> 
                        <span  slot="reference" class="spanBox disBlock textOverflow textMaxWidth" @click="operate('4', item)">{{item.caseTitle}}</span>
                      </el-popover>
                        </td>
                      <td>
                        <template v-for="(type, index) in item.featureType " >
                          <div :key="index" class="noWrap textLeft mTB10">
                            <span class=" mR20">{{type.type}}</span>
                            (<span >{{type.scene}}</span>)
                          </div>
                        </template>
                      </td>
                    
                      <td>
                        <el-popover
                      placement="top-start"
                      title
                      width="200"
                      trigger="hover"
                      :content="item.caseSource"
                    >
                      <span slot="reference" class="textOverflow  disBlock textMaxWidth">{{item.caseSource}}</span>
                    </el-popover>
                  </td>
                      <td>{{item.startTime}}</td>
                      <td>{{item.endTime}}</td>
                      <td>{{item.collectTime}}</td>
                      <td>{{item.ifShow=='true'?'是':'否'}}</td>
                      <td>{{item.manager}}</td>
                      <td>
                        <el-popover
                        placement="top-start"
                        title
                        width="200"
                        trigger="hover"
                        :content="item.content"> 
                       <span slot="reference"  class=" textOverflow disBlock textMaxWidth">{{item.content}}</span>
                        </el-popover>
                      </td>
                      <td class="spanBox" @click="watchFiles(item)">查看</td>
                      <td>
                        <el-popover
                        placement="top-start"
                        title
                        width="200"
                        trigger="hover"
                        :content="item.createTime"> 
                       <span slot="reference"  class=" textOverflow disBlock textMaxWidth">{{item.createTime}}</span>
                        </el-popover>
                      </td>
                      <td>{{item.creator}}</td>
                      <td>
                          <el-popover
                        placement="top-start"
                        title
                        width="200"
                        trigger="hover"
                        :content="item.updateTime"> 
                       <span slot="reference"  class=" textOverflow disBlock textMaxWidth">{{item.updateTime}}</span>
                        </el-popover>
                      </td>
                      <td>{{item.updater}}</td>
                    </tr>
                  </template>
                </tbody>
              </table>
            </Row>
          </div>
           <div class="flexCenter pageBox">
            <Page :total="rowCount" 
            :page-size="pageSize" @on-change="pageChange" @on-page-size-change='pageSizefun'
            :current.sync="currentpage"
            show-elevator show-sizer></Page>
          </div>
        </div>
      </div>
    </div>
    <Modal :mask-closable="false"  v-model="showModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: percentWidth}"
    >
      <component
        :is="tabview"
        ref="dialogModal"
        @cancel="cancelDialog"
        @save="saveDatas"
        :file-obj="dataObj"
        :data-obj="dataObj"
        :key="keyId"
      ></component>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";
import addCaseLibManage from "./common/caseLibManage/addCaseLibManage";
import caseLibManageDetail from "./common/caseLibManage/caseLibManageDetail";
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  created() {
    this.getDic();
    this.handleSubmit(0, 1);
    this.getExtractUnit();
  },
  computed: {},
  components: {
    addCaseLibManage,
    caseLibManageDetail,
    showUsualFiles,
    Treeselect
  },
  watch: {},
  data() {
    return {
      ifExacte:0,
      percentWidth:'60%',
      modelTitle:'新增',
      keyId:"",
      tabview: "addCaseLibManage",
      showModal: false,
      data1: [],
      dataObj: {
        type: "1", //1新增，2修改，3删除， 4详情
        data: []
      },
      pubOrgDatas: [],//管理部门
      pageSize:10,
      currentpage:1,
      pageNum:1,
      rowCount:0,
      formValidate: {
        caseTitle: "", //案例标题
        industry: "", //涉及金融行业
        permissionTime: ["",""], //案件开始时间
        permissionTime1: ["",""], //案件结束时间
        permissionTime2: ["",""], //案件收集时间
        featureType: "", //特征类型
        manager: [], //管理部门
      },
      //   ============弹框一
      tableHeader: [
        { name: "序" },
        { name: "操作" },
        { name: "涉及金融行业" },
        { name: "案例标题" },
        { name: "特征类型" },
        { name: "案例来源" },
        { name: "案例开始时间" },
        { name: "案例结束时间" },
        { name: "案例收集时间" },
        { name: "是否公开		" },
        { name: "管理部门" },
        { name: "编辑内容" },
        { name: "附件" },
        { name: "创建时间" },
        { name: "创建人员" },
        { name: "更新时间" },
        { name: "更新人员" }
      ],
      tableBody: [
      ]
    };
  },
  methods: {
    exportFn(){
      let that = this;
      let data = {
        ifExacte:this.ifExacte,
        minStartTime:this.formValidate.permissionTime[0],
        maxStartTime:this.formValidate.permissionTime[1],
        minEndTime:this.formValidate.permissionTime1[0],
        maxEndTime:this.formValidate.permissionTime1[1],
        minCollectTime:this.formValidate.permissionTime2[0],
        maxCollectTime:this.formValidate.permissionTime2[1],
        manager:this.formValidate.manager,
        industry:this.formValidate.industry,
        pageNum:this.pageNum,
        caseTitle:this.formValidate.caseTitle.trim(),
        pageSize:this.pageSize,
        featureType:this.formValidate.featureType
      };
      data = JSON.stringify(data);
      this.axios
        .post("/process/infCase/getInfCaseExcelList", data, {
          headers: { "Content-Type": "application/json" },
          responseType: "blob"
        })
        .then(response => {
          this.download(response,'导出文档.xls');
        })
        .catch(error => {});
    },
     download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data],{type: 'application/vnd.ms-excel'}));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    getExtractUnit() {
      this.axios
        .post("/system/unit/getExtractUnitAll")
        .then(response => {
          if (response.code == "200") {
            this.data1 = response.data;
            this.selectTreeCleanUp(this.data1);
          }
        })
        .catch(error => {});
    },
    selectTreeCleanUp(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children && treeDatas[i].children.length > 0) {
          this.selectTreeCleanUp(treeDatas[i].children);
        } else this.$delete(treeDatas[i], "children");
      }
    },
    /**
     * name:获取下拉字典
     */
    getDic(){
      this.axios
      .get("/system/getDic/PROCESS_PUB_ORG")
      .then(response => {
        if (response.code == "200") {
          this.pubOrgDatas = response.data.PROCESS_PUB_ORG;
        }
      })
      .catch(function(error) {});
    },
    watchFiles(item){
      this.percentWidth = '40%';
      this.modelTitle = '文件列表';
      this.tabview = "showUsualFiles";
      this.showModal = true;
      this.keyId = item.busId;
      this.dataObj = {
        data:item,
        showFlag:false
      }
    },
    pageSizefun(pageSize){
      this.pageSize = pageSize * 1;
      this.handleSubmit(0, 1);
    },
    pageChange(pageNo){
      this.pageNum = pageNo;
      this.handleSubmit(0, pageNo)
    },
    /**
     * name:查询方法
     * ifExacte:	 0 普通 1 精确
     * pageNo: 页数(1)
     * * */
    handleSubmit(ifExacte, pageNo){
      let data = {
        ifExacte:ifExacte,
        minStartTime:this.formValidate.permissionTime[0],
        maxStartTime:this.formValidate.permissionTime[1],
        minEndTime:this.formValidate.permissionTime1[0],
        maxEndTime:this.formValidate.permissionTime1[1],
        minCollectTime:this.formValidate.permissionTime2[0],
        maxCollectTime:this.formValidate.permissionTime2[1],
        manager:this.formValidate.manager,
        industry:this.formValidate.industry,
        pageNum:pageNo,
        caseTitle:this.formValidate.caseTitle.trim(),
        pageSize:this.pageSize,
        featureType:this.formValidate.featureType
      }
      // console.log('data', data);
      this.axios
      .post("/process/infCase/getInfCaseList", data, {
          headers: { "Content-Type": "application/json" }
        })
      .then(response => {
        if (response.code == "200") {
          this.tableBody = response.data.records;
          this.tableBody.forEach(element => {
            element.createTime = element.createTime.substr(0,19);
            element.updateTime = element.updateTime.substr(0,19);
            element.featureType = JSON.parse(element.featureType);
          });
          this.rowCount = response.data.total;
        }
      })
      .catch(error => {});
    },
    operate(type, item) {
      switch (type) {
        case "1":
          this.keyId = !this.keyId;
          this.percentWidth = '60%';
          this.modelTitle = "新增";
          this.tabview = "addCaseLibManage";
          this.showModal = true;
          this.dataObj = {
            type: 1,
            data: [],
            showFlag:true
          };
          break;
        case "2":
          this.keyId = item.id;
          this.percentWidth = '60%';
          this.modelTitle = "修改";
          this.dataObj = {
            type: 2,
            data: item,
            showFlag:true
          };
          this.tabview = "addCaseLibManage";
          this.showModal = true;
          break;
        case "3":
          this.$Modal.confirm({
            title: "是否删除？",
            okText: "确认",
            cancelText: "取消",
            onOk: () => {
              this.deleteItem(item);
            },
            onCancel: () => {}
          });
          break;
        case "4":
          this.percentWidth = '50%';
          this.keyId = item.id;
          this.modelTitle = "详情";
          this.dataObj = {
            type: 4,
            data: item,
            showFlag:false

          };
          this.tabview = "caseLibManageDetail";
          this.showModal = true;
          break;
      }
    },
    deleteItem(item){
      // console.log('item', item);
      if(item.id){
        this.axios
        .post("/process/infCase/deleteInfCase",{id:item.id},{
            headers: { "Content-Type": "application/json" }
          })
          .then(response =>{
            this.$Message.success("删除成功！");
            this.handleSubmit(0, 1);
            this.currentpage = 1;
            this.pageNum = 1;
          })
          .catch(error=>{
            console.log(error);
          })
      }
    },
    saveDatas() {
      this.showModal = false;
      this.pageNum = 1;
      this.currentpage = 1;
      this.handleSubmit(0,1);
      this.cancel();
    },
    cancelDialog(val) {
      this.showModal = val;
      this.cancel();
    },
    cancel(){
      if(this.$refs['dialogModal'].$refs['formValidate']){
        this.$refs['dialogModal'].$refs['formValidate'].resetFields();
        this.keyId = !this.keyId;
      }
    }
  }
};
</script>
<style scoped>
.rightTopbtn {
  margin: 15px 4px;
  float: right;
}
.btnClass {
  display: flex;
  justify-content: center;
  padding: 20px 0;
}
.operationSpan {
  color: blue;
  margin: 0 5px;
  cursor: pointer;
}
.roleBox {
  padding: 3px 0;
  cursor: pointer;
  color: blue;
}
.roleBox:hover {
  background-color: RGB(241, 243, 244);
}
</style>



