<template>
  <div class="add-case-mangae">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="120"
      class="form-content"
    >
      <FormItem label="涉及金融行业" prop="industry">
        <Input v-model="formValidate.industry" placeholder="请输入涉及金融行业"></Input>
      </FormItem>
      <FormItem label="案例标题" prop="caseTitle">
        <Input v-model="formValidate.caseTitle" placeholder="请输入案例标题"></Input>
      </FormItem>
      <FormItem label="案例来源" prop="caseSource" >
        <Input v-model="formValidate.caseSource" placeholder="请输入案例来源"></Input>
      </FormItem>
      <FormItem label="案例时间范围" prop="round">
        <DatePicker
                split-panels
                type="daterange"
                placeholder="请选择时间"
                :value="formValidate.round"
                @on-change="formValidate.round=$event"
          ></DatePicker>
      </FormItem>
      <!-- <FormItem label="案例结束时间" prop="endTime" >
        <DatePicker
          type="date"
          :options="endTimeOptions"
          placeholder="请选择案例结束时间"
          v-model="formValidate.endTime"
           @on-change="endTimeChange"
        ></DatePicker>
      </FormItem> -->
      <FormItem label="案例收集时间" prop="collectTime" >
        <DatePicker
          type="date"
          placeholder="请选择案例收集时间"
          :value="formValidate.collectTime"
          @on-change="formValidate.collectTime=$event"
        ></DatePicker>
      </FormItem>
      <Row>
        <Row style="padding-bottom:10px;padding-right:10px; box-sizing: border-box;">
          <Button type="dashed" icon="md-add" size="small" style="float:right" @click="addpeoplefun" >新增</Button>
        </Row>
        <FormItem label="特征类型" prop="featureType" >
        <Row class="flexStart">
              <Row class="processAddPeople2">
                <Row v-for="(item,index) in formValidate.featureType" class='flexStart' :key='index' style="border:1px dashed #E8EAEC">
                  <Row style="width:70%" class="flexStartWrap">
                    <Row class="flexCenter processAddPeople-cell-cell">
                      <p class="processAddPeople3">特征类型</p>
                      <Input v-model="item.type" placeholder style="width:50%" size="small" />
                    </Row>
                    <Row class="flexCenter processAddPeople-cell-cell">
                      <p class="processAddPeople3">特征场景</p>
                      <Input v-model="item.scene" placeholder style="width:50%" size="small" />
                    </Row>
                  </Row>
                  <Row style="width:30%;text-align: center;">
                      <Button type="dashed" icon="ios-close" size="small" style="float:right;margin-right:10px;margin-top:23px" @click='delPeoplefun(index)'>删除</Button>
                  </Row>
                </Row>
              </Row>
            </Row>
      </FormItem>
          </Row>
      </Row>
      <FormItem label="编辑内容" prop="content">
        <Input
          v-model="formValidate.content"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder="请输入编辑内容"
        ></Input>
      </FormItem>
      <FormItem label="是否公开" prop="ifShow">
       <i-switch  v-model="formValidate.ifShow" @on-change="changeUseSts(formValidate.ifShow)" >
            <span slot="open">是</span>
            <span slot="close">否</span>
        </i-switch>
      </FormItem>
      <FormItem label="附件">
        <showUsualFiles :file-obj="fileObj" :key="fileId" @requestUploadList="receiveUploadList"></showUsualFiles>
      </FormItem>
      <FormItem>
        <div class="button-content">
          <Button type="primary" @click="handleSubmit('formValidate')" :disabled="buttonDisabled">保存</Button>
          <Button @click="handleCancel">取消</Button>
        </div>
      </FormItem>
    </Form>
  </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";
export default {
  components: {
    showUsualFiles
  },
  props: ["dataObj"],
  watch: {
    "formValidate.round": function(newValue, oldValue) {
      this.$refs["formValidate"].validateField("round");
    }
  },
  data() {
    return {
      buttonDisabled:false,
      startTimeOptions: {}, //开始日期设置
      endTimeOptions: {}, //结束日期设置
      fileObj:{},
      fileId:"",
      uploadList: [],
      formValidate: {
        attachId: "",//附件id
        busId: "",//业务Id
        caseSource: "",//案例来源
        caseTitle: "",//案例标题
        collectTime: "",//案例收集时间
        content: "",//内容
        round: ["",""],
        // startTime: "",//案例开始时间
        // endTime: "",//案例结束时间
        featureType: [{ type: "", scene: "" }], //特征类型
        ifShow: false,//是否显示
        industry: "",//涉及金融行业
      },
      ruleValidate: {
        industry: [
          {
            required: true,
            message: "涉及金融行业不能为空！",
            trigger: "blur"
          },
           { max:252, message: '输入长度过长', trigger: 'blur' },
        ],
        caseTitle: [
          {
            required: true,
            message: "业务代码不能为空！",
            trigger: "blur"
          },
           { max:252, message: '输入长度过长', trigger: 'blur' },
        ],
        caseSource: [
          {
            required: true,
            message: "案例来源不能为空！",
            trigger: "blur"
          },
           { max:252, message: '输入长度过长', trigger: 'blur' },
        ],
        startTime: [
          {
            required: true,
            message: "案例开始时间不能为空！",
            trigger: "change"
          }
        ],
        round: [
          {
            required: true,
            validator: (rule, value, callback) => {
              if (this.formValidate.round[0] == "") {
                return callback(new Error("请选择时间"));
              } else {
                callback();
              }
            },
            trigger: "xxxx"
          }
        ],
        endTime: [
          {
            required: true,
            message: "案例结束时间不能为空！",
            trigger: "change"
          }
        ],
        collectTime: [
          {
            required: true,
            message: "案例收集时间不能为空！",
            trigger: "change"
          }
        ],
        content: [
          {
            required: false,
            message: "编辑内容不能超过1000字！",
            max:1000,
            trigger: "blur"
          },
        ]
      }
    };
  },
  created() {
    // 1:新增 2修改
    if (this.dataObj.type == "2") {
      let datas = this.dataObj.data;
      if(datas && datas.id){
        this.getInfInfoRow(datas.id);
      }
    }else{
      this.fileObj = {
        data:[],
        showFlag:true
      }
    } 
  },
  methods: {
    startTimeChange: function(e) { //设置开始时间
        this.formValidate.startTime = e;
        let startTime = this.formValidate.startTime ? new Date(this.formValidate.startTime).valueOf():'';
        this.endTimeOptions = {
          disabledDate: date => {
            return date && startTime!='' && (date.valueOf() < startTime);
          }
        }
      },
      endTimeChange: function(e) { //设置结束时间
        this.formValidate.endTime = e;
        let endTime = this.formValidate.endtime ? new Date(this.formValidate.endtime).valueOf():'';
        this.startTimeOptions = {
          disabledDate(date) {
            return date && endTime!='' && date.valueOf() > endTime;
          }
        }
      },
    receiveUploadList(list) {
      this.uploadList = [];
      list.forEach(item => {
        this.uploadList.push(item.url);
      });
      this.formValidate.attachId = this.uploadList;
      console.log("receiveUploadlist", this.uploadList);
    },
    delPeoplefun(index){
       this.formValidate.featureType.splice(index,1);
    },
    addpeoplefun(){
      this.formValidate.featureType.push({ type: "", scene: ""})
    },
    handleReset(name) {
      this.$refs[name].resetFields();
    },
    getInfInfoRow(id) {
      this.axios
      .post("/process/infCase/getEntityRow/" + id, {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == "200") {
          let item = response.data;
          this.formValidate = {
            id:item.id,
            attachId:item.attachId,//文件id
            busId: item.busId,//业务Id
            caseSource: item.caseSource,//案例来源
            caseTitle: item.caseTitle,//案例标题
            collectTime: item.collectTime,//案例收集时间
            content: item.content,//内容
            round: [item.startTime, item.endTime],
            // startTime: item.startTime,//案例开始时间
            // endTime: item.endTime,//案例结束时间
            featureType: JSON.parse(item.featureType), //特征类型
            ifShow: item.ifShow == "true" ? true : false, //是否公开
            industry: item.industry,//涉及金融行业
          }
          this.fileObj = {
            data:item,
            showFlag:true,
          }
          this.fileId = item.busId;
        }
      })
      .catch(error => {});

    },
    /**
     * name:保存新增信息
     **/
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          // 新增
          this.buttonDisabled = true;
          this.formValidate.startTime = this.formValidate.round[0];
          this.formValidate.endTime = this.formValidate.round[1];
          if(this.dataObj.type == "1"){
            this.axios
              .post("/process/infCase/addInfCase", this.formValidate, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                  this.buttonDisabled = false;
                  this.$emit("save");
                  this.$Message.success("保存成功！");
                }
              })
              .catch(error => {
                 this.buttonDisabled = false;
              });
          }else{
            // 修改
             this.buttonDisabled = true;
            this.axios
              .post("/process/infCase/updateInfCase", this.formValidate, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                   this.buttonDisabled = false;
                  this.$Message.success("修改成功！");
                  this.$emit("save");
                }
              })
              .catch(error => {
                 this.buttonDisabled = false;
              });
          }
        }
      });
    },
    handleCancel(name) {
      this.$emit("cancel", false);
    },
    changeUseSts(flg) {
      this.formValidate.ifShow = flg;
    },
  }
};
</script>
<style lang="scss" @scoped>
.add-case-mangae {
  margin: 0 30px;
  label {
    font-size: 14px;
  }
  .form-content {
    .speical-span {
      width: 8.33333333%;
      float: left;
    }
    .ivu-date-picker {
      width: 100%;
    }
    .special-div{
      width:100px;
      font-size: 14px;
      padding: 10px 12px 10px 0;
      text-align: right;
    }
    .special-form{
      margin:0 0 20px -20px;
      border:1px solid #EEEEEE;
      padding: 20px 20px 0px 0px;
    }
  }
  .processAddPeople1{
    width: 130px;
  }
  .processAddPeople2{
    width: 100%;
  }
  .processAddPeople3{
    width: 40%;
  }
}
</style>


