<template>
    <div class="common-detail">
        <div class="content">
                <Form :label-width="150">
                    <FormItem label="涉及金融行业：">
                        <label>{{resData.industry}}</label>
                    </FormItem>
                    <FormItem label="案例标题：">
                        <label>{{resData.caseTitle}}</label>
                    </FormItem>
                    <FormItem label="是否公开：">
                        <label>{{resData.ifShow=='true'?'是':'否'}}</label>
                    </FormItem>
                    <FormItem label="案例来源：">
                        <label>{{resData.caseSource}}</label>
                    </FormItem>
                    <FormItem label="案例开始时间：">
                       <label>{{resData.startTime}}</label>
                    </FormItem>
                    <FormItem label="案例结束时间：">
                        <label>{{resData.endTime}}</label>
                    </FormItem>
                    <FormItem label="案例收集时间：">
                        <label>{{resData.collectTime}}</label>
                    </FormItem>
                    <FormItem label="特征类型：">
                        <template v-for="(item, index) in resData.featureType " >
                          <div :key="index">
                            <span class="detailSpan mR20">特征类型：{{item.type}}</span>
                            <span class="detailSpan">特征场景：{{item.scene}}</span>
                          </div>
                        </template>
                    </FormItem>
                    <FormItem label="编辑内容：">
                       <label>
                         <!-- {{resData.content}} -->
                  <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="resData.content"
                  >
                    <p slot="reference" class="textMaxWidth2 textOverflow" >{{resData.content}}</p>
                  </el-popover>
                         </label>
                    </FormItem>
                    <FormItem label="附件：">
                            <showUsualFiles :file-obj="fileObj" :key="keyId"></showUsualFiles>
                    </FormItem>
                </Form>
            </div>
    </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";

export default {
  props: ["dataObj"],
  components: {
    showUsualFiles
  },
  data() {
    return {
      keyId: "",
      resData: {},
      fileObj:{},
    };
  },
  created() {
    if (Object.keys(this.dataObj.data).length > 0) {
      let datas = this.dataObj.data;
      if (datas && datas.id) {
        // 调用浏览次数接口
        if(this.dataObj && this.dataObj.infViewNum){
          this.getInfInfoRowNum(datas.id);
        }else{
          this.getInfInfoRow(datas.id);
        }
      }
    }
  },
  methods: {
    getInfInfoRowNum(id){
      this.axios
        .post("/process/infCase/getEntityRow/" + id +'?infViewNum=1', {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.resData = response.data;
            this.resData.createTime = this.resData.createTime.substr(0,19)
            this.resData.featureType = JSON.parse(response.data.featureType);
            this.keyId = response.data.busId;
            this.fileObj = {
                data:response.data,
                showFlag:false,
            }
          }
        })
        .catch(error => {});
    },
    getInfInfoRow(id) {
      this.axios
        .post("/process/infCase/getEntityRow/" + id +'?decode=1', {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.resData = response.data;
            this.resData.createTime = this.resData.createTime.substr(0,19)
            this.resData.featureType = JSON.parse(response.data.featureType);
            this.keyId = response.data.busId;
            this.fileObj = {
                data:response.data,
                showFlag:false,
            }
          }
        })
        .catch(error => {});
    }
  }
};
</script>


